import test from 'ava';
import {
    getApi,
    auth,
    logout,
    responseErrMessage as err
} from '../shortcuts';

test.before('Add authorization token to request', async t => {
    t.context.api = await getApi();
    auth(t.context.api);
});

test('Create feed works', async t => {
    const feed = { name: 'autotest-feed', description: 'feed generated by api autotest'};
    const resp  = await t.context.api.post('/feeds', feed);
    t.is(resp.status, 201, err(resp));
});

test('Create feed fails without token', async t => {
    logout(t.context.api);
    const feed = { name: 'autotest-feed12', description: 'feed generated by api autotest12'};
    const resp  = await t.context.api.post('/feeds', feed);
    t.is(resp.status, 401, err(resp));
});

test('Create feed fails with wrong inputs', async t => {
    const feed = { name: null, description: null};
    const resp  = await t.context.api.post('/feeds', feed);
    t.is(resp.status, 400, err(resp));
});

test('Get all feeds works', async t => {
    const resp = await t.context.api.get('/feeds');
    t.is(resp.status, 200, err(resp));
    t.is(resp.data[0].owner.username, 'korneed');
});

test.only('Delete feed works', async t => {
    const feed = { name: 'autotest-feed-with-data', description: 'feed with data generated by api autotest'};
    const respFeed  = await t.context.api.post('/feeds', feed);
    t.is(respFeed.status, 201, err(respFeed));
    const feedKey = respFeed.data.key;

    const respDelete = await t.context.api.delete(`/feeds/${feedKey}`);
    t.is(respDelete.status, 200, err(respDelete));
});

