test('Add data to feed works', async t => {
    const feed = { name: 'autotest-feed-with-data', description: 'feed with data generated by api autotest'};
    const respFeed  = await t.context.api.post('/feeds', feed);
    t.is(respFeed.status, 201, err(respFeed));
    const feedKey = respFeed.data.key;

    const data = {value: 42};
    const respData = await t.context.api.post(`/feeds/${feedKey}/data`, data);
    t.is(respData.status, 200, err(respData));
    t.is(respData.data.value, data.value.toString());
    t.is(respData.data.feed_key, feedKey);
});
